import React from 'react'

import { useState, useEffect } from 'react'

export default ({handlechanger,addBlog}) => {

    const [title,setTitle] = useState("")

    const [description, setDescription]= useState("")

    let onChange=(e)=>{

        let obj=e.target
       if(obj.classList.contains("blogName")){
           
           setTitle(obj.value)
       }else if(obj.classList.contains("description")){
          setDescription(obj.value)
       }

       console.log(title , description)
    }

    useEffect(()=>{
        handlechanger(title,description)
    })
    

  return (
    <section className="formBody">
      
    <div className="inputs" onChange={onChange}>

        <h1>Add a new blog</h1>

     
         
         <div className="input">
          
             <input type="text" className="blogName" placeholder="Blog Title"></input>
         </div>

         <div className="input">
          
          <textarea  placeholder="Blog description" className='description'></textarea>
      </div>

      <button className="btn" onClick={addBlog}>Add blog</button>

        


       </div>
        </section>
  )
}
