import React, { useContext, useEffect, useState } from 'react'
import Data from '../../../../Api'
import { Context } from '../../../../Context/Context'

import { useNavigate } from 'react-router-dom'

import BlogPost from './BlogPost'

import Pagination from "react-bootstrap/Pagination";


export default () => {

  const [blogs,setBlogs]= useState([])

  const [user, setUser]= useContext(Context)

  const [newBlogs, setNew] = useState([])

  const [paginatioBlogs, setPaginationBlogs] = useState([]);

  const navigate= useNavigate()

  useEffect(()=>{
    getBlogs()
    pagination(1)
  },[])

  

  

  const getBlogs= async () =>{

    try{

      let data= new Data()

      let blogs= await data.getBlogs()

      setBlogs(blogs)

    }catch{

    }
  } 

  const add = () => {
    navigate("/addBlog");
  };


  const delBlog = async (id) => {
    try {
      let data = new Data();

      let blogs = await data.getCourses();

      let blog = blogs.filter((e) => e.id == id);

      let newBlogs = blogs.filter((e) => e.id != id);

      setNew(newBlogs);

      await data.deleteBlog(blog[0]);
    } catch (e) {
      console.log(e);
    }
  };
  useEffect(()=>{
    getBlogs()
  },(newBlogs))

  let pagination = async (number) => {

    try{

      let data= new Data()

      let blogs = await data.getBlogs()

    let nou = [];
    for (let i = 3 * (number - 1); i < 3 * number && i < blogs.length; i++) {
      nou.push(blogs[i]);
    }
    // console.log(nou);
    setPaginationBlogs(nou);
      
    }catch(e){

      throw new Error(e)
    }
   
  };

  let paginationButtons = () =>{

    let active = 1;
    let items = [];
    let total= 0;

    if(blogs.length>0){
       total= Math.floor(blogs.length/4)+1
    }else{
       total= Math.floor(blogs.length/4)+1
    }

    

    for (let number = 1; number <= total; number++) {
      items.push(
        <Pagination.Item
          key={number}
          active={number == active}
          onClick={() => {
            pagination(number);
          }}
        >
          {number}
        </Pagination.Item>
      );
    }

    return items;

  }


  return (
    <>
    <section class="blog">
     
    <h3>Our blog</h3>
    <h1>Recent posts from blog</h1>


    <div class="blogPosts">

      {

        paginatioBlogs.length>0?(
          paginatioBlogs.map(e=>{
            return <BlogPost  blogs={e} key={blogs.id} delBlog={delBlog}/>
          })
        )

        
        :(
           <p>NU s-au postat bloguri momentan</p>
           
        )}    


{(() => {
          if (user) {
            if (user.role_id == 2) {
              return (
                <button className="enroll" onClick={add}>
                  Add new blog
                </button>
              );
            }
          }
        })()}
        

    </div>

    <div className="pagination">
        <Pagination>{paginationButtons()}</Pagination>
      </div>

         
    </section>

</>
    
  )
}
