import React, { useContext, useEffect, useState } from 'react'

import AddBlogBody from './AddBlogBody'

import UpperHeader from '../../HeaderComponents/UpperHeader'
import { Context } from '../../../../Context/Context'
import Data from '../../../../Api'

import Footer from '../../Footer'

import { useNavigate } from 'react-router-dom'

export default () => {

    const [user,setUser] = useContext(Context)

    const [title,setTitle] = useState("")

    const [createdBy, setCreator]= useState("")

    const [date,setDate] = useState("")

    const [description, setDescription]= useState("")

    const navigate= useNavigate()

    useEffect(()=>{
        if(user){

            setCreator(user.lastName)
            getCurrentTimestamp()
        }
    })

    const getCurrentTimestamp = () => {
        const date = new Date();
    
        const year =
          date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    
        setDate(year);
      };

      const handlechanger = (title,description) =>{

      
      
        setTitle(title)
        setDescription(description)
       
       }

       const addBlog= async () =>{
             
        try{              
            let data= new Data()

             await data.addBlog({title,createdBy,date,description})

            navigate("/")    

        }catch(e){

            throw new Error(e)
        }
     }


  return (
    <section className="overlay">
      <UpperHeader />
      <AddBlogBody  handlechanger={handlechanger} addBlog={addBlog} />

      <Footer />
     
    </section>
  )
}
